// src/main.js

import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import { createVuetify } from 'vuetify';
import * as components from 'vuetify/components';
import * as directives from 'vuetify/directives';
import '@mdi/font/css/materialdesignicons.css';
import 'vuetify/styles'; // Ensure you are using css-loader

// Import Apollo Client configuration
import { ApolloClient, InMemoryCache, HttpLink } from '@apollo/client/core';
import { provideApolloClient } from '@vue/apollo-composable';

// Create an HTTP link to your GraphQL server
const httpLink = new HttpLink({
  uri: 'https://your-graphql-endpoint.com/graphql', // Replace with your GraphQL endpoint
});

// Create an Apollo Client instance
const apolloClient = new ApolloClient({
  link: httpLink,
  cache: new InMemoryCache(),
});

// Provide the Apollo Client instance to the Vue application
provideApolloClient(apolloClient);

const app = createApp(App);

const vuetify = createVuetify({
  components,
  directives,
});

app.use(router);
app.use(vuetify);

app.mount('#app');



