<!-- src/components/ImageUploader.vue -->
<template>
  <div>
    <input type="file" @change="onFileChange" />
    <button @click="uploadImage">Upload</button>
    <p v-if="uploading">Uploading...</p>
    <p v-if="imageUrl">Image URL: <a :href="imageUrl" target="_blank">{{ imageUrl }}</a></p>
    <p v-if="error">{{ error }}</p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ImageUploader',
  data() {
    return {
      file: null,
      imageUrl: '',
      uploading: false,
      error: '',
    };
  },
  methods: {
    onFileChange(event) {
      this.file = event.target.files[0];
    },
    async uploadImage() {
      if (!this.file) {
        this.error = 'Please select an image file first.';
        return;
      }

      this.uploading = true;
      this.error = '';

      const formData = new FormData();
      formData.append('file', this.file);
      formData.append('upload_preset', 'YOUR_UPLOAD_PRESET'); // Replace with your actual upload preset

      try {
        const response = await axios.post(
          'https://api.cloudinary.com/v1_1/alvin-tan/image/upload',
          formData,
          {
            headers: {
              'X-Requested-With': 'XMLHttpRequest',
            },
          }
        );
        this.imageUrl = response.data.secure_url;
      } catch (error) {
        this.error = 'Error uploading image: ' + error.message;
        console.error('Error uploading image:', error.response ? error.response.data : error.message);
      } finally {
        this.uploading = false;
      }
    },
  },
};
</script>

