<template>
    <v-container fluid class="d-flex align-center justify-center" style="height: 100vh;">
      <v-row justify="center" align="center">
        <v-col
          v-for="(image, index) in images"
          :key="index"
          cols="12"
          sm="4"
          class="d-flex justify-center"
        >
          <v-img
            :src="image.src"
            :alt="image.alt"
            class="selectable-image"
            @click="navigateTo(image.route, image.param)"
          ></v-img>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  export default {
    name: "MainPage",
    data() {
      return {
        images: [
          { src: 'img/home.jpeg', alt: 'Image 1', route: '/home', param: 'HOME' },
          { src: 'img/business.jpeg', alt: 'Image 2', route: '/home', param: 'BUSINESS' },
          { src: 'img/winback.jpeg', alt: 'Image 3', route: '/home', param: 'WIN BACK' },
        ],
      };
    },
    methods: {
      navigateTo(route, param) {
        this.$router.push({ path: route, query: { param: param } });
      },
    },
  };
  </script>

  <style scoped>
  .selectable-image {
    cursor: pointer;
    width: 100%;
    height: auto;
    border-radius: 8px;
    transition: transform 0.2s;
  }
  
  .selectable-image:hover {
    transform: scale(1.05);
  }
  </style>