<template>
  <div class="pricing-container">
    <section v-for="(plan, index) in plans" :key="index" :id="'pricing-section-' + (index + 1)" class="pricing-section">
      <div class="pricing-table">
        <div class="pricing-card">
          <div class="pricing-details">
            <div v-for="(feature, fIndex) in plan.features" :key="fIndex">
              <div v-if="isImage(feature)">
                <div class="image-and-text">
                  <img :src="getImagePath(feature).src" :alt="plan.title + ' feature image'" :style="getImageStyle(feature)" class="feature-image" />
                  <h2>{{ getImagePath(feature).text }}</h2>
                </div>
              </div>
              <div v-else-if="isSvg(feature)">
                <div class="svg-and-text">
                  <div v-html="getSvgContent(feature).svg"></div>
                  <span>{{ getSvgContent(feature).text }}</span>
                </div>
              </div>
              <div v-else-if="isHtmlTag(feature)">
                <component :is="getHtmlTagName(feature)">
                  {{ getHtmlTagContent(feature) }}
                </component>
              </div>
              <div v-else>
                {{ feature }}
              </div>
            </div>
            <div class="pricing-info">
              <p>{{ plan.promo }}</p>
              <p>{{ plan.price }}</p>
            </div>
            <div class="pricing-content">
              <p>{{ plan.extra }}</p>
            </div>
            <div class="pricing-action">
              <button class="apply-now-btn" @click="fillPackageName(plan.title)">Apply Now</button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'MyPackage',
  props: {
    pageTitle: {
      type: String,
      default: ''
    },
    plans: {
      type: Array,
      required: true
    }
  },
  

  methods: {
    fillPackageName(packageName) {
      console.log(packageName);
      this.packageName = packageName;
      document.getElementById('registration-form').scrollIntoView({ behavior: 'smooth' });
    },
    isImage(feature) {
      return feature.startsWith('image:');
    },
    getImagePath(feature) {
      const parts = feature.split(',');
      const src = parts[0].split('image:')[1].trim();
      const text = parts[3] ? parts[3].trim() : '';
      return { src, text };
    },
    getImageStyle(feature) {
      const parts = feature.split(',');
      const width = parts[1] ? parts[1].split('=')[1].trim() + 'px' : 'auto';
      const height = parts[2] ? parts[2].split('=')[1].trim() + 'px' : 'auto';
      return { width, height };
    },
    isSvg(feature) {
      return feature.startsWith('svg:');
    },
    getSvgContent(feature) {
      const [svgPart, ...textParts] = feature.split('svg:')[1].split(',');
      const svgAttributes = svgPart.trim();
      const svgContent = textParts.shift().trim();
      return {
        svg: `<svg ${svgAttributes}>${svgContent}</svg>`,
        text: textParts.join(',').trim()
      };
    },
    isHtmlTag(feature) {
      return feature.match(/^[a-zA-Z0-9]+:/);
    },
    getHtmlTagName(feature) {
      return feature.split(':')[0];
    },
    getHtmlTagContent(feature) {
      return feature.split(':')[1].trim();
    }
  }
};
</script>

